<script>
// @ is an alias to /src
import Header from '@/components/Header.vue'
import { mapState, mapActions, mapMutations } from 'vuex'
import AccountSetLayout from '@/components/AccountSet/AccountSetLayout.vue'
import BasiceLayout from '@/components/Common/BasicLayout.vue'
import { Message } from 'element-ui'
import ImageCode from '@/components/ImageCode.vue'
import SendEmailCode from '@/components/SendEmailCode.vue'
import { LANG } from '../config/contant.js'

export default {
  name: 'home',
  components: {
    Header,
    BasiceLayout,
    AccountSetLayout,
    ImageCode,
    SendEmailCode
  },
  computed: mapState({
    address: state => state.wallet.address,
    email: state => state.account.email,
    eth_verify_status: state => state.wallet.eth_verify_status
  }),
  data() {
    return {
      new_eth_address: '',
      // inputImageCode: '',
      inputEmailCode: '',
      ticket: '', // ticket
      csnonce: '' //
    }
  },
  created() {
    this.getWalletAddress()

    this.getVertifUrl({ action: 2 }).then(res => {
      this.csnonce = res.data.csnonce
      var newScript = document.createElement('script')
      newScript.type = 'text/javascript'
      newScript.src = res.data.url
      document.body.appendChild(newScript)
      let that = this

      setTimeout(() => {
        var capOption = {
          callback: cbfn,
          themeColor: '15bcad',
          lang: LANG[this.$i18n.locale || 'en']
        }
        capInit(document.getElementById('TCaptcha'), capOption)
        //
        function cbfn(retJson) {
          if (retJson.ret == 0) {
            that.ticket = retJson.ticket
            // that.sendCode();
            // 
          } else {
            //
          }
        }
      }, 1000)
    })
  },
  methods: {
    ...mapActions([
      'getWalletAddress',
      'bindWalletAddress',
      'getVertifUrl',
      'commitUnbindAddress'
    ]),
    bindWallet(text) {
      console.log(this.$i18n.messages)

      // let { password, new_eth_address } = this;
      let { ticket, inputEmailCode, new_eth_address } = this

      if (!inputEmailCode || !new_eth_address) {
        Message(this.$t('errorTipsAboutBind'))
        return
      }

      if (new_eth_address.length !== 42) {
        Message('new wallet address is error')
        return
      }
      //  
      this.$confirm(text, {
        confirmButtonText: this.$t('walletConfirm'),
        cancelButtonText: this.$t('walletCancel')
      })
        .then(() => {
          this.bindWalletAddress({
            new_eth_address,
            emailVerifyCode: inputEmailCode
          }).then(res => {
            if (res.code === 200) {
              Message({
                type: 'success',
                message: 'bind success'
              })
              this.getWalletAddress()
            } else {
              Message({
                type: 'error',
                message: res.message || 'bind error'
              })
            }
          })
        })
        .catch(() => {
          console.log('cancel')
        })
    },
    // 
    emailCodeTip(error) {
      console.log(error)
      if (error.message) {
        Message({
          type: 'error',
          message: error.message
        })
      }
    },
    // 
    unbindWallet() {
      this.$confirm(this.$t('unbindTips'), {
        confirmButtonText: this.$t('confirm'),
        cancelButtonText: this.$t('cancel')
      }).then(() => {
        this.commitUnbindAddress().then(res => {
          if (res.code === 200) {
            // // messageTips('', this.$i18n.locale);
            Message({
              type: 'success',
              message: this.$t('unbindSuccess')
            })
            this.getWalletAddress()
            this.getCaptcha()
          } else {
            Message({
              type: 'error',
              message: res.message || this.$t('unbindError')
            })
            this.getWalletAddress()
          }
        })
      })
    },
    // 
    getCaptcha() {
      this.getVertifUrl({ action: 2 }).then(res => {
        this.csnonce = res.data.csnonce
        let newScript = document.createElement('script')
        newScript.type = 'text/javascript'
        newScript.src = res.data.url
        document.body.appendChild(newScript)
        let that = this

        setTimeout(() => {
          var capOption = {
            callback: cbfn,
            themeColor: '15bcad',
            lang: LANG[this.$i18n.locale || 'en']
          }
          capInit(document.getElementById('TCaptcha'), capOption)
          //
          function cbfn(retJson) {
            if (retJson.ret == 0) {
              that.ticket = retJson.ticket
            }
          }
        }, 1000)
      })
    }
  }
}
</script>

